import React  from "react";

import './MorsePage.scss';
import { IReducers } from "../../../redux/store";
import { connect } from "react-redux";
import { BottomBar } from "../../SharedComponents/BottomBar/BottomBar";
import { MorseMenu } from "../../SharedComponents/MorseMenu/MorseMenu";
import {Morse, MorseTextInput} from "../../SharedComponents/MorseTextInput/MorseTextInput";
import circle from '../../../assets/oval.svg';
import dash from '../../../assets/rectangle.svg';
import red_circle from '../../../assets/red-oval.svg';
import red_dash from '../../../assets/red-rectangle.svg';
import { ALPHA_TO_MORSE, isMobile, isSamsungBrowser } from "../../../assets/Constants";
import { userChoiceTypeAction } from "../../../redux/actions/MainAction";
import { RouteComponentProps } from "react-router-dom";

import arrow from '../../../assets/images/arrow-white.svg';
import arrow_grey from '../../../assets/images/arrow-grey.svg';
import arrow_black from '../../../assets/images/arrow-black-2.svg';
import {Card, ChainDetails} from "../../../models/Interfaces";

interface Props {
    userChoice:any;
    // typeChoice:(type:string)=>void;
    onMorseType:(chainPriceAndWord:any)=>void;
    card: Card;
    step: number;
    handleBack: ()=>void;
    handleNext: ()=>void;
    finishMobileButton:boolean;
    buyAction:Function;
    selectEventMobile:boolean;
    isAfterBuyPage:boolean;
    resetMorse:boolean;
    isBuyPage:boolean;
    firstChain:any;
    secondChain:any;
    // inputRef:()
}

interface State {
    morseHover:boolean;
    x:number;
    y:number;
    morseMenuOpen: boolean;
    isAfterBuy : boolean;
    firstChain: {
        chain: string;
        chainPriceAndWord: any;
        indexError: number;
    },
    secondChain: {
        chain: string;
        chainPriceAndWord: any;
        indexError: number;
    }

}

export class MorsePage extends React.Component<Props,State>{


    constructor(props:Props){
        super(props);

        this.state = {
            morseHover:false,
            x:0,
            y:0,
            morseMenuOpen: false,
            isAfterBuy: false,
            firstChain: {
                chain: '', indexError: -1, chainPriceAndWord: {text: '', length: 0, totalPrice: 0,dashSum:0,dotSum:0}
            },
            secondChain: {
                chain: '', indexError: -1, chainPriceAndWord: {text: '', length: 0, totalPrice: 0,dashSum:0,dotSum:0}
            },
        }
    }


    handleMouseMove = (e:any) => {
        this.setState({
          x: e.clientX,
          y: e.clientY
        });
      };

    handleChange = async (chain:string,indexError:number,chainDetails:ChainDetails) => {

        const chainPriceAndWord = {text: chain, length: chainDetails.length, totalPrice: chainDetails.totalPrice,dashSum:chainDetails.dashSum,dotSum:chainDetails.dotSum}

        if (this.props.card.duplicateMorse){
            await this.setState({secondChain: {chain, indexError, chainPriceAndWord} ,firstChain: {chain, indexError, chainPriceAndWord}})

        }else {
            if (this.props.step === 1) {
                await this.setState({firstChain: {chain, indexError, chainPriceAndWord}})
            } else {
                await this.setState({secondChain: {chain, indexError, chainPriceAndWord}})
            }
        
        }
        this.props.onMorseType(this.state);

    }

    handleBuy = () => {
        const isAfterBuy = this.props.buyAction()
        
        this.setState({isAfterBuy: true})
    }


    componentDidUpdate(prevProps:Props){
        if(prevProps.selectEventMobile !=this.props.selectEventMobile&&isMobile){
            if(this.props.finishMobileButton){
                this.props.handleNext()
            }
        }

 




        if(this.props.resetMorse){
            this.state = {
                morseHover:false,
                x:0,
                y:0,
                morseMenuOpen: false,
                isAfterBuy: false,
                firstChain: {
                    chain: '', 
                    indexError: -1, 
                    chainPriceAndWord: {text: '', length: 0, totalPrice: 0,dashSum:0,dotSum:0}
                },
                secondChain: {
                    chain: '', 
                    indexError: -1, 
                    chainPriceAndWord: {text: '', length: 0, totalPrice: 0,dashSum:0,dotSum:0}
                },
            }
        }
    }

    buttons = () => {

        const enable:boolean = this.state.firstChain.chain.length>0 &&(this.state.firstChain.indexError===-1&&this.state.secondChain.indexError===-1)
        const enable2:boolean = this.props.card.stepsNumber === 2?this.state.secondChain.chain.length>0 &&(this.state.secondChain.indexError===-1&&this.state.secondChain.indexError===-1):true;
            if(this.props.step > this.props.card.stepsNumber)
                return <div className="buy-morse" onClick={this.handleBuy}> BUY <img src={arrow_black} className="arrow"/></div>
        return <React.Fragment>
            {this.props.step === this.props.card.stepsNumber ?
                <div className={`${enable&&enable2?'next':'next-disable'}`} 
                     onClick={enable&&enable2?this.props.handleNext:()=>{}} > 
                     DONE 
                     <img src={enable&&enable2?arrow:arrow_grey} className="arrow"/>
                </div> :
                <div onClick={enable?this.props.handleNext:()=>{}}
                    className={`${enable?'next':'next-disable'}`}> 
                    NEXT EAR
                 <img src={enable?arrow:arrow_grey} className="arrow"/>
                 </div>}
            {<div className={`${this.props.step === this.props.card.stepsNumber&&this.props.step ===2?'back':'back-disable'}`}
             onClick={this.props.handleBack}>
                <img src={arrow_grey} className="arrow left-arrow"/> BACK </div>}
        </React.Fragment>
    }

    inputFoucs(inputRef:any){
        if(inputRef&&!this.state.morseMenuOpen)
           inputRef.focus(); 
    }

    getTextMargin = () => {
        return  0;
    }


    shiftMargin = ()=>{
        return  this.props.isBuyPage&&!isMobile?'25vw':0;
    }



    render(){
      

        return (
            <div className={`container-morse ${this.props.card.stepsNumber === 2 ? "container-morse-mini-text" :"" }`}
            style={{height:(!isSamsungBrowser)&&isMobile?'87vh':'100vh',backdropFilter:this.state.isAfterBuy?'brightness(.5)':'none'}}>
               {this.state.isAfterBuy||this.props.isAfterBuyPage ? <BackToShop/> :
                <React.Fragment>
                    {!this.props.isBuyPage&&
                <div className="question" style={{opacity: this.state.firstChain.chain.length<1? 1:.4}}>WHAT'S YOUR SECRET? </div>}
                <div className="morse-texts">
                    {this.props.step === 2 && this.props.card.stepsNumber !== 1 && 
                    <div className='first-morse'>
                        <Morse text={this.state.firstChain.chain} indexError={30}/>
                    </div>}
                    {<MorseTextInput hidePlaceholder={false} inputRef={this.inputFoucs.bind(this)}
                                     className={`morse-input ${this.props.step === 1 ? '' : 'display-none'}`}
                                     onChange={this.handleChange}/>}
                    {this.props.card.stepsNumber !== 1 &&
                    <MorseTextInput inputRef={this.inputFoucs.bind(this)} hidePlaceholder={true}
                                    className={`morse-input ${this.props.step === 2 ? '' : ' display-none'}`}
                                    onChange={this.handleChange}/>}
                   </div>


                <MorseChain 
                    card={this.props.card}
                    className={this.props.card.rightNecklaceClass}
                    getTextMargin={this.getTextMargin}
                    shiftMargin={this.shiftMargin}
                    chain={this.props.firstChain?this.props.firstChain.chain:""}
                    step={this.props.step}
                />

                <MorseChain 
                    card={this.props.card}
                    className={this.props.card.leftNecklaceClass}
                    getTextMargin={this.getTextMargin}
                    shiftMargin={this.shiftMargin}
                    chain={this.props.secondChain?this.props.secondChain.chain:""}
                    step={this.props.step}
                />

                <div className='morse-btns'>{this.buttons()}</div>
                {<div className={"morse-menu-icon"}  
                onMouseEnter={() => this.setState({morseHover: true})}
                onMouseLeave={() => this.setState({morseHover: false})}
                onMouseMove={this.handleMouseMove} onClick={() => {
                    this.setState({morseMenuOpen: true})
                }}></div>}

                
                {<MorseMenu className={this.state.morseMenuOpen ? '' : 'hide-menu'} 
                    onClose={() => {
                        this.setState({morseMenuOpen: false})
                    }}/>}
                </React.Fragment>}


                {this.state.morseHover &&<div className="mouse-movement" 
                        style={{top:this.state.y-6,left:this.state.x-190}}> 
                         MORSE GUIDE
                    </div>}
            </div>
        );
    }
}

const DotIcon = React.memo((props:any) => {
    const isFill = props.fill;
    return (
        <svg className='morse-char dot-icon' xmlns="http://www.w3.org/2000/svg" width="8" height="8" viewBox="0 0 8 8">
            <circle cx="972" cy="383" r="4" style={{fill:isFill?'white':''}} fill-rule="evenodd" transform="translate(-968 -379)"/>
        </svg>

    );
})
const DashIcon = React.memo((props:any) => {
    const isFill = props.fill;
    return (
        <svg className='morse-char dash-svg' width="8" height="24">
        <rect width="8" height="24" style={{fill:isFill?'white':''}}/>
        </svg>

    );
})

  const mapStateToProps = (state:IReducers) => {
    const  {slides,userChoice} = state.mainState;
    console.log("userChoice",userChoice)
    return {
        slides,userChoice
    };
  };
  
  const mapDispatchToProps = (dispatch:any) => {
    
    return {
        typeChoice:(type:ChainDetails)=>dispatch(userChoiceTypeAction(type)),

    };
  };


  const BackToShop = React.memo((props) => {

    return (
        <div className="back-to-shop-card">
            <Morse text={"THANK YOU"} indexError={30}/>
            <div className="description">YOU WERE REDIRECTED TO THE PAYMENT PAGE TO COMPLETE YOUR ORDER</div>
            <div onClick={()=> window.location.reload()} className="back-to-shop">
             CONTINUE SHOPPING 
             <img src={arrow_black} className="arrow"/>
            </div>
        </div>
    );
})

const MorseChain = React.memo((props:any) => {
    return (
        <div className="background-morse-wrapper">
            <div className={`paint-morse ${props.className}`}
                style={{marginTop: `${props.getTextMargin()}px`,marginRight:props.shiftMargin()}}>
                <div className='morse-border'>
                    {props.chain.split('').map((c: string, index:any) => {
                        return <div key={c + index}
                                    className={`tasti ${props.step === props.card.stepsNumber&&index==props.chain.split('').length-1 ? 'white-morse-fill' : 'grey-morse-fill'} `} >
                            {ALPHA_TO_MORSE[c.toUpperCase()].split('').map((p) => {

                                if(p === '.')
                                    return   <DotIcon fill={index==props.chain.split('').length-1}/>
                                else if(p === '-')
                                    return  <DashIcon fill={index==props.chain.split('').length-1}/>
                                else
                                    return ''
                            })}
                        </div>
                    })}
                </div>
            </div>
       </div>
    );
})

  

  export default connect(mapStateToProps,mapDispatchToProps)(MorsePage);
