export const  typeToString:any  = (key:TYPE)=> {

   switch(key){
       
       case TYPE.WHITE_GOLD :
       return "WHITE GOLD"

        case TYPE.YELLOW_GOLD :
        return "YELLOW GOLD"

        case TYPE.EARRING :
       return "EARRING"

        case TYPE.NECKLACE :
       return "NECKLACE"

        case TYPE.DISTINCT_PAIR :
       return "DISTINCT PAIR"

        case TYPE.IDENTICAL_PAIR :
       return "IDENTICAL PAIR"

        case TYPE.SINGLE_NECKLACE :
       return "SINGLE NECKLACE";

        case TYPE.SINGLE_EARRING :
       return "SINGLE EARRING";
    
       default:
           return null
   }
   
}

export const  stringToType:any  = (stringType:string)=> {

    switch(stringType){
        case "WHITE GOLD" :
        return TYPE.WHITE_GOLD 
 
        case "YELLOW GOLD" :
         return TYPE.YELLOW_GOLD 
 
         case "EARRING" :
         return TYPE.EARRING 
 
        case "NECKLACE" :
         return TYPE.NECKLACE 
 
        case "DISTINCT PAIR" :
         return TYPE.DISTINCT_PAIR 
 
        case "IDENTICAL PAIR" :
         return TYPE.IDENTICAL_PAIR 
 
        case "SINGLE NECKLACE" :
         return TYPE.SINGLE_NECKLACE 
 
        case "SINGLE EARRING" :
         return TYPE.SINGLE_EARRING 
     
        default:
            return TYPE.EARRING 

    }
    
 }


export const  remainingTypes:any = (key:TYPE)=> {

    switch(key){
        case TYPE.WHITE_GOLD :
        return [{key:TYPE.YELLOW_GOLD,displayName:"YELLOW GOLD"}]
 
         case TYPE.YELLOW_GOLD :
         return [{key:TYPE.WHITE_GOLD,displayName:"WHITE GOLD"}]
         case TYPE.GOLD :
         return [{key:TYPE.WHITE_GOLD,displayName:"WHITE GOLD"},
         {key:TYPE.YELLOW_GOLD,displayName:"YELLOW GOLD"}]
 
         case TYPE.EARRING :
        return [
            {key:TYPE.NECKLACE,displayName:"NECKLACE"},
            {key:TYPE.DISTINCT_PAIR,displayName:"EARRINGS - DISTINCT PAIR"},
            {key:TYPE.IDENTICAL_PAIR,displayName:"EARRINGS - IDENTICAL PAIR"},
            {key:TYPE.SINGLE_EARRING,displayName:"EARRINGS - SINGLE EARRING"},
        ]
        case TYPE.NECKLACE :
        return [
            {key:TYPE.DISTINCT_PAIR,displayName:"EARRINGS - DISTINCT PAIR"},
            {key:TYPE.IDENTICAL_PAIR,displayName:"EARRINGS - IDENTICAL PAIR"},
            {key:TYPE.SINGLE_EARRING,displayName:"EARRINGS - SINGLE EARRING"},
        ]
 
         case TYPE.DISTINCT_PAIR :
        return [
            {key:TYPE.NECKLACE,displayName:"NECKLACE"},
            {key:TYPE.IDENTICAL_PAIR,displayName:"EARRINGS - IDENTICAL PAIR"},
            {key:TYPE.SINGLE_EARRING,displayName:"EARRINGS - SINGLE EARRING"},
        ]
 
         case TYPE.IDENTICAL_PAIR :
        return [
            {key:TYPE.NECKLACE,displayName:"NECKLACE"},
            {key:TYPE.DISTINCT_PAIR,displayName:"EARRINGS - DISTINCT PAIR"},
            {key:TYPE.SINGLE_EARRING,displayName:"EARRINGS - SINGLE EARRING"},
        ]
 
        case TYPE.SINGLE_EARRING :
        return [
            {key:TYPE.NECKLACE,displayName:"NECKLACE"},
            {key:TYPE.DISTINCT_PAIR,displayName:"EARRINGS - DISTINCT PAIR"},
            {key:TYPE.IDENTICAL_PAIR,displayName:"EARRINGS - IDENTICAL PAIR"},
        ]
        default:
            return {}
    }
    
 }
