import './ColumnGrid.scss';
import React from 'react';
import { CardComponent } from '../Card/CardComponent';
import { Card,TYPE, Slider, typeToString } from '../../../models/Interfaces';
import MorsePage from '../../screens/MorsePage/MorsePage';
import {RouteComponentProps} from "react-router";
import { isMobile, isSamsungBrowser } from '../../../assets/Constants';
import { MaterialComponent } from '../Material/MaterialComponent';
import arrow_black from '../../../assets/images/arrow-black-2.svg';

interface Props {
    slides:any;
    onClick:Function;
    slideIndex: number;
    onMorseType:(chainPriceAndWord:any)=>void;
    onBack: Function;
    selectMobileButton:boolean;
    finishMobile:boolean;
    buyAction:Function;
    isAfterBuyPage: boolean;
    selectedBottom: any,
    onEdit: boolean;
    onMaterialClicked:Function;
    firstChain:any;
    secondChain:any;
    isMaterialPage:boolean;
    // isMaterialPage:boolean;
}

interface State  {
    x:number;
    y:number;
    mouseText:string;
    mouseDirection:string;
    oldX:number;
    isCardSelected: boolean;
    selectedCard: number;
    step: number
    currentType:any;
    currentIndex:number;
    finishMobileButton:boolean;
    resetMorse:boolean;
    isMaterialPage:boolean;
    buybtn:boolean;
    hover:boolean;
}

export class FirstColumnGrid extends React.Component<Props&RouteComponentProps,State>{
    unlisten: any
    constructor(props:Props&RouteComponentProps){
        super(props);
        this.state = {
            step: 1,
            isCardSelected: false,
            selectedCard: -1,
            x:0,y:0,mouseText:'',
            mouseDirection:'left',
            oldX:0,
            currentType:null,
            currentIndex:-1,
            finishMobileButton:false,
            resetMorse:false,
            isMaterialPage:false,buybtn:false,
            hover:false
        }
    }

    handleMouseMove = (e:any) => {
        this.setState({
          x: e.clientX,
          y: e.clientY
        });
        this.getMouseDirection(e)
      };

    componentDidMount(): void {
        if(this.props.selectedBottom !== null ){
            const slide = this.props.slides[0]
            const index = slide.cards.findIndex((c: Card) => c.type === this.props.selectedBottom)
            if(slide.cards[index]) {
                this.handleSelect(slide.cards[index], index, true).then()
            }
        }



        this.unlisten = this.props.history.listen((e, b)=>{
            if(b ==='POP') {
                this.backLogic();
            }
        })
    }
    componentWillUnmount(): void {
        this.unlisten();
    }

    componentDidUpdate(prevProps:Props){

        if(prevProps.onEdit !== this.props.onEdit){
            this.handleBack();
        }


        if(this.props.selectedBottom !== null && prevProps.selectedBottom !== this.props.selectedBottom){
            const slide = this.props.slides[this.props.selectedBottom === TYPE.NECKLACE ? 0 : 1]
            const index = slide.cards.findIndex((c: Card) => c.type === this.props.selectedBottom)
            if(slide.cards[index]) {
                this.handleSelect(slide.cards[index], index, true).then()
            }
            this.setState({resetMorse:true})
            this.setState({resetMorse:false})
        }



        if(prevProps.isMaterialPage != this.props.isMaterialPage&&isMobile){

            if(this.props.isMaterialPage){
                this.handleFinish()
                this.setState({buybtn:true,isMaterialPage:false})
            }

        }

        if(prevProps.selectMobileButton !=this.props.selectMobileButton&&isMobile){
            this.setState({finishMobileButton:this.props.finishMobile})
            if(this.state.currentType === TYPE.NECKLACE&&!this.props.finishMobile) {
            this.setState({isCardSelected: true, selectedCard: this.state.currentIndex})
            this.props.history.push('/category')
            }
        }
    }


    backLogic = ()=>{
        if (this.state.isCardSelected){
            this.setState({isCardSelected: false,step: 1, selectedCard: -1,isMaterialPage:false,buybtn:false})
            this.props.history.replace('/')
        }
        else {
            this.props.onBack();
        }
    }



    handleSelect = async (c: Card, index: number, forceOpen = false) => {
        await this.setState({currentType:c.type,currentIndex:index})
        if(!isMobile){
        if(c.type === TYPE.NECKLACE || forceOpen) {

            await this.setState({isCardSelected: true, selectedCard: index})
        }
        this.props.history.push('/category')
        }
        this.props.onClick(typeToString(c.type))
    }

    getNe = (index: number) => {
        if(this.state.isCardSelected)
        return this.state.selectedCard === index ? 'full-width' : 'no-width'
        return ''
    }

    handleNext = () => {
        this.setState(prevState=> ({...prevState, step: prevState.step + 1}));

        if(!this.state.isMaterialPage)
            this.setState({isMaterialPage:true})

        // console.log("here")
    }

    handleFinish = ()=>{
        this.setState(prevState=> ({...prevState, step: prevState.step + 1}));
    }

    handleBack = () => {
        this.backLogic();
    }


    handleMaterial = (material:string)=>{
            this.props.onMaterialClicked(material);
        if(!isMobile){

            this.handleFinish()
            this.setState({buybtn:true,isMaterialPage:false})

            console.log("heress in material",material)
        }


    }


    render(){

        // console.log(this.props.firstChain)
        const slide =this.props.slides[this.props.slideIndex]
        return (
            <div className="grid-container first-card-background"  onMouseMove={this.handleMouseMove}>
                {!this.state.isCardSelected && !this.state.isMaterialPage && !this.state.buybtn && !this.state.hover && <div className="question"
                onClick={() => this.handleSelect(this.state.currentType?this.state.currentType:TYPE.EARRING, 0)}
                >{this.props.slides[this.props.slideIndex].title}</div>}

                {this.props.slides[this.props.slideIndex].cards.map((c:Card, index: number)=>{
                    return (
                        <div  className={`mini-item ${this.getNe(index)}`} style={{height:(!isSamsungBrowser&&isMobile)?'79vh':'100vh',animation: `${!isSamsungBrowser&&isMobile?'mymove-chrome':'mymove'} ${2 - index + 2}s`,}}>
                                <CardComponent
                                    mouseText={typeToString(c.type)}
                                    selectedCard={true}
                                    isCardSelected={this.state.isCardSelected}
                                    backgroundImage={c.image}
                                    onMouseMove={(mouseText: string) => {
                                        this.setState({mouseText: mouseText})
                                        this.setState({currentType:c.type,currentIndex:index})
                                    }}
                                    cardType={c.type}
                                    onMouseEnter={()=>{if(!isMobile) this.setState({hover:true})}}
                                    onMouseLeave={()=>{if(!isMobile)this.setState({hover:false})}}
                                    isBuyPage={this.state.step + 1 === c.stepsNumber}
                                    isAfterBuyPage={this.props.isAfterBuyPage}
                                    onClick={() => this.handleSelect(c, index)}
                                    isMaterialPage={this.state.isMaterialPage||this.state.buybtn}
                                    buyPage={this.state.buybtn&& !this.props.isAfterBuyPage}
                                    />
                                    </div>
                    )
                })}

                 {this.state.buybtn&& !this.props.isAfterBuyPage && <div className='morse-btns'>
                        <div className="buy-morse" onClick={this.props.buyAction.bind(this)}> BUY <img src={arrow_black} className="arrow"/></div>
                    </div>}

                {this.state.isMaterialPage &&
                 <MaterialComponent
                    slider={this.props.slides[this.props.slideIndex]}
                    onClick={this.handleMaterial}
                    />}
                {this.state.isCardSelected && (!this.state.isMaterialPage||this.state.buybtn) &&
                <MorsePage selectEventMobile={this.props.selectMobileButton}
                resetMorse={this.state.resetMorse}
                buyAction={this.props.buyAction.bind(this)}
                finishMobileButton={this.state.finishMobileButton}
                handleBack={this.handleBack} handleNext={this.handleNext}
                step={this.state.step}
                card={slide.cards[this.state.selectedCard]}
                isAfterBuyPage={this.props.isAfterBuyPage}
                 onMorseType={this.props.onMorseType.bind(this)}
                 isBuyPage={this.state.buybtn}
                 firstChain={this.props.firstChain}
                 secondChain={this.props.secondChain}
                 />}


                {!this.state.isCardSelected &&
                    <div className="mouse-movement"
                        style={{top:this.state.y-6,left:this.state.mouseDirection ==="right"? this.state.x+25:this.state.mouseText.length<13?this.state.x-235: this.state.x-195}}>
                        SELECT {this.state.mouseText}
                    </div>}

            </div>
        );
    }



    getMouseDirection(e:any) {
        //deal with the horizontal case
        // console.log('1111' , e.pageX,e.target.getBoundingClientRect().left,e.target.clientWidth,e);
        if (this.state.oldX < e.pageX) {
            this.setState({mouseDirection:"left"});
        } else if (this.state.oldX > e.pageX){
            this.setState({mouseDirection:"right"});
        }
        this.setState({oldX:e.pageX});
    }


}

