import React from 'react';
import './CardComponent.scss';
import { TYPE } from '../../../models/Interfaces';

interface Props {
    backgroundImage:string;
    mouseText:string;
    onMouseMove:Function;
    onClick:Function;
    selectedCard: boolean;
    isCardSelected: boolean;
    isBuyPage: boolean;
    isAfterBuyPage: boolean;
    isMaterialPage:boolean
    onMouseEnter:Function;
    onMouseLeave:Function;
    cardType:TYPE;
    buyPage:boolean;
}

interface State  {
    hover:boolean,
}

export class CardComponent extends React.Component<Props,State>{

    getClassName = () => {
        if (this.props.isAfterBuyPage)
            return ""
        if (this.props.isMaterialPage)
            return "full-light"
        return ""
    }


    constructor(props:Props){
        super(props);
        this.state = {hover:false}
    }

    componentDidUpdate(prevProps:Props){
    }


    getSelectedCardClass = () => {
        if (this.props.isCardSelected){
            return this.props.selectedCard ? 'full-width' : 'no-width'
        }
        return ''
    }

    render(){
        return (
            <div className={`card-wrapper ${this.getSelectedCardClass()} ${this.state.hover ? "cover" : ""} ${this.props.isMaterialPage ? "full-light" : ""} ${this.getClassName()}`}
             onClick={this.props.onClick.bind(this)} 
             onMouseMove={()=>this.props.onMouseMove(this.props.mouseText)}>
                    <div className={`item-column ${this.props.isAfterBuyPage||this.props.isMaterialPage ? "center-woman": ""}  
                    ${this.props.isCardSelected&&this.props.cardType===TYPE.NECKLACE?'necklace-move':'other-move'}`}
                         style={{backgroundImage: `url(${this.props.backgroundImage})`,}}
                         onMouseEnter={() => {
                             this.setState({hover: true});
                             this.props.onMouseEnter();
                            }}
                         onMouseLeave={() => {
                             this.setState({hover: false})
                             this.props.onMouseLeave();
                             }}>
                        <div className="gradiant"/>
                        {!this.state.hover&& !this.props.buyPage &&<div className="layer"/>}

                        {!this.state.hover && !this.props.isCardSelected &&
                         <div className='off-line'>
                            <div className="line hidden"/>
                            <div className="line hide-line-mobile"/>
                            <div className="line hide-line-mobile"/>
                            <div className="line"/>
                            <div className="line hide-line-mobile"/>
                            <div className="line hide-line-mobile"/>
                            <div className="line hidden"/>
                        </div>}
                    </div>
                    {this.state.hover && 
                    <div className="type-mobile">
                        {this.props.mouseText}
                    </div>}
        </div>

        );
    }
}
