import React  from "react";

import './HomePage.scss'
import { IReducers } from "../../../redux/store";
import { connect } from "react-redux";
import { BottomBar } from "../../SharedComponents/BottomBar/BottomBar";
import { Types,typeToString, TYPE } from "../../../models/Interfaces";
import { userChoiceCategoryAction, userChoiceStyleAction, userChoiceMaterialAction } from "../../../redux/actions/MainAction";
import { RouteComponentProps} from "react-router";
import {FirstColumnGrid} from "../../SharedComponents/GridComponent/FirstColumnGrid";
import { SecondColumnGrid } from "../../SharedComponents/GridComponent/SecondColumnGrid";
import { isMobile, isSamsungBrowser, createPopupWin } from "../../../assets/Constants";
import arrow from '../../../assets/images/arrow-white.svg';
import * as shop from '../../../Services/shopify';

interface Props {
    slides:any[],
    categoryChoice:(category:string)=>void,
    styleChoice:(style:string)=>void,
    materialChoice:(material:string)=>void,
    userChoice:any
}

interface State  {
    slideIndex:number;
    userChoice:any;
    chainPriceAndDetails:any|null;
    cardClicked:boolean;
    selectClicked:boolean;
    currentType:string;
    buttonText:string;
    hideBottomBar:boolean;
    showProductDetails:boolean;
    isAfterBuyPage:boolean;
    selectedBottom: any;
    onEdit: boolean;
    clickedMaterial:boolean;
    currentMaterial:string;
    isMaterial:boolean;
}

export class HomePage extends React.Component<Props&RouteComponentProps,State>{

    unlisten: any

    constructor(props:Props&RouteComponentProps){
        super(props);
        this.state = {slideIndex:0,
            userChoice:undefined,
            chainPriceAndDetails:null,
            cardClicked:false,
            currentType:'',
            selectClicked:false,
            buttonText:'SELECT',
            hideBottomBar:false,
            showProductDetails:false,
            isAfterBuyPage : false,
            selectedBottom: null,
            onEdit: false,
            clickedMaterial:false,
            currentMaterial:'',
            isMaterial:false
        }

    }


    resetChain(){
        this.setState({chainPriceAndDetails:null})
    }
    componentDidMount(){
        this.setState({userChoice:this.props.userChoice});
        this.unlisten = this.props.history.listen((e, b)=>{
            if(b ==='POP') {
            this.resetChain()
            }
        })
    }

    componentWillUnmount(): void {
        this.unlisten()
    }

    handleEditBtn = () => {
        this.setState(prevState => ({...prevState,showProductDetails: false, hideBottomBar: false, onEdit: !prevState.onEdit}))
    }
      
    onBack = () => {
        this.setState(prevState => ({...prevState, slideIndex: prevState.slideIndex - 1}))
        this.resetChain();
    }

     onClick(type:string){
        this.setState({cardClicked:true,currentType:type})
        if(!isMobile||(this.state.selectClicked)){
            if(this.props.slides[this.state.slideIndex].screenType === Types.CATEGORY) {
                this.props.categoryChoice(type);
            }
            if(this.props.slides[this.state.slideIndex].screenType === Types.MATERTIAL){
                this.props.materialChoice(type);
            }
            if(this.props.slides[this.state.slideIndex].screenType === Types.STYLE){
                this.props.styleChoice(type);
            }
            if(this.state.slideIndex < 1 && type !== typeToString(TYPE.NECKLACE)){
                 this.setState({slideIndex:(this.state.slideIndex+1)})
            }
         }
    }

    handleBottomBarClicked = (value:any)=>{
        switch(value.keyinput){
            case "style":
                if(value.value.key === TYPE.NECKLACE){
                    this.props.categoryChoice(typeToString(value.value.key))
                    this.props.styleChoice("");
                    this.setState({slideIndex: 0})
                }else{
                    this.props.categoryChoice(typeToString(TYPE.EARRING))
                    this.props.styleChoice(typeToString(value.value.key));
                }
                this.resetChain()
                this.setState({selectedBottom: value.value.key})
                break;
            case "material":
                this.props.materialChoice(typeToString(value.value.key))
                break;
        }
    }

    isTyping = ()=>{

        if(this.state.chainPriceAndDetails){
            if(this.state.currentType === typeToString(TYPE.IDENTICAL_PAIR)||this.state.currentType === typeToString(TYPE.DISTINCT_PAIR)){
                if(this.state.chainPriceAndDetails.firstChain.chain.length > 0 ){
                    
                    if(this.state.chainPriceAndDetails.secondChain.chain.length > 0 ){
                        if(this.state.buttonText!=='DONE')
                        this.setState({buttonText:'DONE'})
                       return true;
                    }
                    if(this.state.buttonText!=='NEXT')
                        this.setState({buttonText:'NEXT'})
                    return true;
                }
            }else{
                
                if(this.state.chainPriceAndDetails.firstChain.chain.length > 0 ){
                    if(this.state.buttonText!=='DONE')
                    this.setState({buttonText:'DONE'})
                    return true;
                }
            }
        }

        return false;
    }


    buyAction = async ()=>{
        this.setState({isAfterBuyPage: true,buttonText:'CONTINUE SHOPPING'})
        let dotHandle, dashHandle;
        switch (this.props.userChoice.material) {
            case 'WHITE GOLD':
                dotHandle = 'white-gold-morse-diamond-dot';
                dashHandle = 'white-gold-morse-diamond-dash';
                break;
            default:
                dotHandle = 'diamond-dot';
                dashHandle = 'morse-diamond-dash';
                break;
        }

        Promise.all([// shop.fetchProducts(), 
            shop.createCheckout(), 
            shop.fetchProductByHandle('morse-custom-jewelry'),
            shop.fetchProductByHandle(dotHandle),
            shop.fetchProductByHandle(dashHandle)
        ])
        .then(data => {
            const checkout: any = data[0];
            const morseCustomJewelry = data[1];
            const yellowDot = data[2];
            const yellowDash = data[3];
            const variantId = morseCustomJewelry.variants[0].id
            const yellowDotVariantId = yellowDot.variants[0].id
            const yellowDashVariantId = yellowDash.variants[0].id
            
            // Dummy item with all descriptions
            const morsecjLineItem: any = {
                variantId,
                quantity: 1,
                customAttributes: [] 
            };

            // If user forgot to select material it will be yellow gold
            let material = '';
            if (this.props.userChoice.material) {
                material = this.props.userChoice.material;
            } else {
                material = 'WHITE GOLD';
            }

            // Lets count dots and dashes
            let dotsQty = 0, dashesQty = 0;
            dotsQty = +this.state.chainPriceAndDetails.firstChain.chainPriceAndWord.dotSum;
            dashesQty = +this.state.chainPriceAndDetails.firstChain.chainPriceAndWord.dashSum;

            // Add description for necklace
            if (this.props.userChoice.category === 'NECKLACE') {
                morsecjLineItem.customAttributes.push(
                    { key: 'Necklace', 
                        value: `${material}. ${this.state.chainPriceAndDetails.firstChain.chain.toUpperCase()}` 
                    });
            }

            // Description for earring
            if (this.props.userChoice.category === 'EARRING') {
                morsecjLineItem.customAttributes.push(
                    { key: 'Earring 1', 
                        value: `${material}. ${this.state.chainPriceAndDetails.firstChain.chain.toUpperCase()}` 
                    });
                
                // If there are 2 earrings
                if (this.props.userChoice.style === 'IDENTICAL PAIR' || this.props.userChoice.style === 'DISTINCT PAIR') {
                    morsecjLineItem.customAttributes.push(
                        { key: 'Earring 2', 
                            value: `${material}. ${this.state.chainPriceAndDetails.secondChain.chain.toUpperCase()}` 
                        });
                    
                    // Add some dots
                    dotsQty += +this.state.chainPriceAndDetails.secondChain.chainPriceAndWord.dotSum;
                    dashesQty += +this.state.chainPriceAndDetails.secondChain.chainPriceAndWord.dashSum;
                }
            }

            // Sum it up
            const lineItems = [morsecjLineItem, {
                variantId: yellowDotVariantId,
                quantity: dotsQty,
            }, {
                variantId: yellowDashVariantId,
                quantity: dashesQty,
            }];
            shop.addLineItems(checkout.id.toString(), lineItems)
            .then(cart => {
                createPopupWin(checkout.webUrl,"shopify",window.innerWidth/2,window.innerHeight/2,window.innerHeight/4,window.innerWidth/4);
            });
        });   
        
        return true;

    }

    donePhase = ()=>{
        this.setState({hideBottomBar:true,showProductDetails:true})
    }

    onMaterialClicked = (material:string)=>{

        if(!isMobile){
            this.props.materialChoice(material);
            this.setState({clickedMaterial:true});
        }
        this.setState({cardClicked:true})
        this.setState({currentMaterial:material})
    }


    render(){
        return (

           
            <div className="container" style={{height:(!isSamsungBrowser)&&isMobile?'87vh':'100vh'}}>

                <div className="version">v0.2.0</div>
                <div className="logo"></div>
                <MobileLoading/>
                {this.state.slideIndex === 0 ?
                    <FirstColumnGrid buyAction={this.buyAction.bind(this)} {...this.props} onBack={this.onBack} slideIndex={this.state.slideIndex} slides={this.props.slides}
                                     onMorseType={(chainPriceAndDetails: any) => {
                                        //  console.log("ABC",chainPriceAndDetails)
                                         this.setState({chainPriceAndDetails: chainPriceAndDetails})
                                     }}
                                     onEdit={this.state.onEdit}
                                     isAfterBuyPage={this.state.isAfterBuyPage}
                                     selectMobileButton={this.state.selectClicked}
                                     onClick={(type: string) => {this.onClick(type)}}
                                     selectedBottom={this.state.selectedBottom}
                                     finishMobile={this.state.buttonText === 'NEXT'||this.state.buttonText === 'DONE'}
                                     onMaterialClicked={this.onMaterialClicked}
                                     firstChain={this.state.chainPriceAndDetails?this.state.chainPriceAndDetails.firstChain:{chain:""}}
                                     secondChain={this.state.chainPriceAndDetails?this.state.chainPriceAndDetails.secondChain:{chain:""}}
                                     isMaterialPage={this.state.isMaterial}
                                     /> :
                    <SecondColumnGrid buyAction={this.buyAction.bind(this)} {...this.props} onBack={this.onBack} slideIndex={this.state.slideIndex} slides={this.props.slides}
                                      onMorseType={(chainPriceAndDetails: any) => {
                                          this.setState({chainPriceAndDetails: chainPriceAndDetails})
                                      }}
                                      onEdit={this.state.onEdit}
                                      selectedBottom={this.state.selectedBottom}
                                      isAfterBuyPage={this.state.isAfterBuyPage}
                                      selectMobileButton={this.state.selectClicked}
                                      onClick={(type: string) => {this.onClick(type)}}
                                      finishMobile={this.state.buttonText === 'NEXT'||this.state.buttonText === 'DONE'}
                                      onMaterialClicked={this.onMaterialClicked}
                                      firstChain={this.state.chainPriceAndDetails?this.state.chainPriceAndDetails.firstChain:{chain:""}}
                                      secondChain={this.state.chainPriceAndDetails?this.state.chainPriceAndDetails.secondChain:{chain:""}}
                                      isMaterialPage={this.state.isMaterial}

                                      />
                }  {(!this.state.hideBottomBar||!isMobile||this.state.isAfterBuyPage||!this.state.isMaterial)   && <BottomBar 
                    
                            className="bottom-bar" 
                            userChoice={this.props.userChoice} 
                            selectedType={(value:any)=>{
                                                    this.handleBottomBarClicked(value)
                                                    }}
                           secret={this.state.chainPriceAndDetails}
                           getSelected={async (selected:boolean)=>{
                            await this.setState({selectClicked:selected})

                            
                               if(!this.isTyping()){
                                await  this.onClick(this.state.currentType);
                                this.setState({selectClicked:false,cardClicked:false})
                               }else{
                                await this.setState({selectClicked:false});
                                if(this.state.currentMaterial !== ''){
                                    this.props.materialChoice(this.state.currentMaterial);
                                    await this.setState({clickedMaterial:true,isMaterial:true});
                                    return;
                                }
                                if(this.state.buttonText === 'DONE'){
                                    this.donePhase();
                                 }
                                 if(this.state.buttonText === 'CONTINUE SHOPPING'){
                                    window.location.reload();
                                 }
                               }
                            
                            }}

                        buttonTextMobile={this.state.buttonText}
                        cardClicked={this.state.cardClicked||this.isTyping()}/>}


                       {(this.state.showProductDetails&&isMobile) && !this.state.isAfterBuyPage && this.state.clickedMaterial &&
                       
                       <ProductDetails category={this.props.userChoice.category} 
                       style={this.props.userChoice.style} 
                       material={this.props.userChoice.material}
                       chainPriceAndDetails={this.state.chainPriceAndDetails} 
                       handleEditBtn={this.handleEditBtn}
                       buyAction={this.buyAction}/> }
            </div>
        );
    }
}

const MobileLoading = React.memo((props) => {
    return (
        <div className="mobile-loading">
            {isMobile && <div className='off-line'>
                <div className="line hidden"/>
                <div className="line hide-line-mobile"/>
                <div className="line hide-line-mobile"/>
                <div className="line"/>
                <div className="line hide-line-mobile"/>
                <div className="line hide-line-mobile"/>
                <div className="line hidden"/>
            </div>}
        </div>
    );
})


const ProductDetails = React.memo((props:any) => {
    return (
        <div className="product-details">
        <div className="product-item">{props.category==='NECKLACE'?props.category:props.style}</div>
        <div className="product-item">{props.material!==''?props.material:'MATERIAL'}</div>
        <div className="product-item">
            {props.chainPriceAndDetails.firstChain.chain.toUpperCase()} 
        - {(props.chainPriceAndDetails.firstChain.chainPriceAndWord.length*.1).toFixed(2)}cm 
        {props.chainPriceAndDetails.secondChain.chain!==''?(' '+props.chainPriceAndDetails.secondChain.chain.toUpperCase()+
        ' - ' +(props.chainPriceAndDetails.secondChain.chainPriceAndWord.length*.1).toFixed(2)+'cm'):''}
        </div>
        <div className="product-item">DASH: $980 X{
        props.chainPriceAndDetails.firstChain.chainPriceAndWord.dashSum+
        props.chainPriceAndDetails.secondChain.chainPriceAndWord.dashSum} - DOT : $560 X{
            props.chainPriceAndDetails.firstChain.chainPriceAndWord.dotSum+
            props.chainPriceAndDetails.secondChain.chainPriceAndWord.dotSum}</div>
        <div className="product-item">PRICE: ${props.chainPriceAndDetails.firstChain.chainPriceAndWord.totalPrice+
            props.chainPriceAndDetails.secondChain.chainPriceAndWord.totalPrice}</div>
        <div className="product-item" style={{textDecoration:"underline"}} onClick={props.handleEditBtn}>EDIT</div>
        <div className="buy-morse" onClick={props.buyAction}> BUY <img src={arrow} className="arrow"/></div>
    </div>
    );
})


const mapStateToProps = (state:IReducers) => {
    const  {slides,userChoice} = state.mainState;
    return {
        slides,userChoice
    };
  };
  
  const mapDispatchToProps = (dispatch:any) => {
    
    return {
        categoryChoice:(category:string)=>dispatch(userChoiceCategoryAction(category)),
        styleChoice:(style:string)=>dispatch(userChoiceStyleAction(style)),
        materialChoice:(material:string)=>dispatch(userChoiceMaterialAction(material)),

    };
  };
  
  export default connect(mapStateToProps,mapDispatchToProps)(HomePage);
