

import React from 'react';
import './BottomBar.scss';
import { remainingTypes, stringToType, TYPE } from '../../../models/Interfaces';
import { DOT_PRICE, DASH_PRICE } from '../MorseTextInput/MorseTextInput';
import arrow from '../../../assets/images/arrow-black.svg'//'../../../assets/images/arrow-white.svg';
import dropdown_icon from '../../../assets/images/drop-down.svg'//'../../../assets/images/arrow-white.svg';
import { isMobile } from '../../../assets/Constants';
import { MorseMenu } from '../MorseMenu/MorseMenu';


interface Props {
    className?:string;
    userChoice:any;
    secret:any;
    selectedType:(value:any)=>void;
    cardClicked:Boolean;
    getSelected:(selected:boolean)=>void;
    buttonTextMobile:string;
}

interface State  {
    notSelectedArrayHover:any[];
    isMenuOpen:boolean;
    isMaterialMobileOpen:boolean;
    isCategoryMobileOpen:boolean;
    isSecretMobileOpen:boolean;
}

export class BottomBar extends React.Component<Props,State>{


    constructor(props:Props){
        super(props);
        this.state = {notSelectedArrayHover:[],isMenuOpen:false,isMaterialMobileOpen:false,isCategoryMobileOpen:false,isSecretMobileOpen:false}
    }

    componentDidMount():void {
        this.setState({notSelectedArrayHover:!this.props.userChoice?remainingTypes(stringToType('EARRING'))
        ?
                        this.props.userChoice.style==''?remainingTypes(stringToType(this.props.userChoice.category))
                        :remainingTypes(stringToType(this.props.userChoice.style)):[]:[]})
    }

    handleClickedItem(c:any,type:string){
        this.props.selectedType({value:c,keyinput:type});
    }

    render(){

        const enableCategory = this.props.userChoice.category.length !== 0;
        const enableMaterial = this.props.userChoice.material.length !== 0;
        const enablePrice = this.props.secret &&(this.props.secret.firstChain.chainPriceAndWord.length !== 0); 
        console.log("this.props.userChoice.material",enableMaterial)
        
        return (
           <div className={`bottom-container ${this.props.className?this.props.className:''}`}>
               <div className={`menu-item  ${!enableCategory?'not-active-bottom-item':''}`}>
                    {this.props.userChoice?this.props.userChoice.category==''?'CATEGORY':this.props.userChoice.category:'CATEGORY'}
                    <span>  {this.props.userChoice?this.props.userChoice.style==''?'':'-  '+this.props.userChoice.style:''}</span>

                   {enableCategory&& <div className={`dropdown-content category-width `} >
                        {this.props.userChoice.style?remainingTypes(stringToType(this.props.userChoice.style)).map((c:any)=>{
                            return ( <div className="sub-menu-item dropdown-content-category" onClick={()=>this.handleClickedItem(c,"style")} >{c.displayName}</div>)
                        }):remainingTypes(stringToType(this.props.userChoice.category)).map((c:any)=>{
                            return ( <div className="sub-menu-item dropdown-content-category" onClick={()=>this.handleClickedItem(c,"style")}>{c.displayName}</div>)
                        })}
                   </div>}
                </div>

               <div className={`menu-item ${!enableMaterial?'not-active-bottom-item':''}`}> 
                {enableMaterial?this.props.userChoice.material:'MATERIAL'}
                
                {enableMaterial&&<div className="dropdown-content">
                        {enableMaterial?remainingTypes(stringToType(this.props.userChoice.material)).map((c:any)=>{
                            return ( <div className="sub-menu-item" onClick={()=>this.handleClickedItem(c,"material")}>{c.displayName}</div>)
                        }):remainingTypes(TYPE.GOLD).map((c:any)=>{
                            return ( <div className="sub-menu-item" onClick={()=>this.handleClickedItem(c,"material")}>{c.displayName}</div>)
                        })}
                   </div>}
               </div>

              <div className={`menu-item ${!this.props.secret?'not-active-bottom-item':''}`}> 
                  {this.props.secret?((this.props.secret.firstChain.chainPriceAndWord.text).toUpperCase() +' - '+
                  ((this.props.secret.firstChain.chainPriceAndWord.length*.1).toFixed(2)+'  '+
                  (this.props.secret.secondChain.chainPriceAndWord.length>0?((this.props.secret.secondChain.chainPriceAndWord.text).toUpperCase() +' - '+
                  (this.props.secret.secondChain.chainPriceAndWord.length*.1).toFixed(2)+'cm'):''))):'SECRET'}
               </div>

             <div className={`menu-item end-item ${!enablePrice?'not-active-bottom-item':''}`}> 
               PRICE: {this.props.secret?`\$${(this.props.secret.firstChain.chainPriceAndWord.totalPrice+
                this.props.secret.secondChain.chainPriceAndWord.totalPrice
                )}`:'$0'}
               
               {enablePrice&&  <div className="price-dropdown">
                    <div className="price-item" >DASH STONES:&nbsp; <span className="price-text">{DASH_PRICE}</span> 
                    
                    <span className={"number-of-stones"}>X{enablePrice?(this.props.secret.firstChain.chainPriceAndWord.dashSum+
                        this.props.secret.secondChain.chainPriceAndWord.dashSum
                        ):0}</span></div>
                    <div className="price-item" >
                        
                        DOT STONES:&nbsp;&nbsp;<span className="price-text">{DOT_PRICE}</span> 
                    <span className={"number-of-stones"}>X{enablePrice?(this.props.secret.firstChain.chainPriceAndWord.dotSum
                        +this.props.secret.secondChain.chainPriceAndWord.dotSum
                        ):0}</span>

                    </div>
                  
                </div>}
                </div>


                <div className={`menu-item-mobile morse-menu-item`} onClick={()=>this.setState({isMenuOpen:!this.state.isMenuOpen})}> 
                    <div className="menu-morse-mobile">i</div>
               </div>
               <div className={`menu-item-mobile button-position-mobile ${!this.props.cardClicked?'select-disable':''}`} 
            //    style={{zIndex:99999999999999999}}
                onClick={()=>{
                    // alert("aaa")
                    if(this.props.cardClicked)
                    this.props.getSelected(true);
                }}
               > 
                    {this.props.buttonTextMobile} <img src={arrow} className="arrow"/>
                </div>

                 {this.state.isMenuOpen&&<div className={`mobile-menu`}>
                        <div className="menu-item-sub" onClick={enableCategory?()=>{
                            this.setState({isCategoryMobileOpen:!this.state.isCategoryMobileOpen})
                            this.setState({isSecretMobileOpen:false})
                            this.setState({isMaterialMobileOpen:false})
                            
                        }:()=>{}}>
                            {this.props.userChoice?this.props.userChoice.category==''?'CATEGORY':this.props.userChoice.category:'CATEGORY'}
                            <span>  {this.props.userChoice?this.props.userChoice.style==''?'':'-  '+this.props.userChoice.style:''}</span> 
                            <img src={dropdown_icon} className="dropdown-icon"/>
                        </div>
                        {this.state.isCategoryMobileOpen&& (this.props.userChoice.style?remainingTypes(stringToType(this.props.userChoice.style)).map((c:any)=>{
                            return ( <div className="menu-item-sub menu-item-sub-black" onClick={()=>{
                                this.handleClickedItem(c,"style")
                                window.location.reload()
                            }} >{c.displayName}</div>)
                        }):remainingTypes(stringToType(this.props.userChoice.category)).map((c:any)=>{
                            return ( <div className="menu-item-sub menu-item-sub-black" onClick={()=>{
                                this.handleClickedItem(c,"style")
                                window.location.reload()
                            }}>{c.displayName}</div>)
                        }))}
                        <div className="menu-item-sub" onClick={enablePrice?()=>{
                            this.setState({isSecretMobileOpen:!this.state.isSecretMobileOpen})
                            this.setState({isCategoryMobileOpen:false})
                            this.setState({isMaterialMobileOpen:false})
                            }:()=>{}}>
                            {this.props.secret?((this.props.secret.firstChain.chainPriceAndWord.text).toUpperCase() +' - '+
                            ((this.props.secret.firstChain.chainPriceAndWord.length*.1).toFixed(2)+'  '+
                            (this.props.secret.secondChain.chainPriceAndWord.length>0?((this.props.secret.secondChain.chainPriceAndWord.text).toUpperCase() +' - '+
                            (this.props.secret.secondChain.chainPriceAndWord.length*.1).toFixed(2)+'cm'):''))):'SECRET'}
                            <img src={dropdown_icon} className="dropdown-icon"/>
                        </div>
                        {this.state.isSecretMobileOpen&& (
                                <div className="menu-item-sub menu-item-sub-black" onClick={()=>{
                                    console.log("edit")
                                }}>EDIT</div>
                            )}
                        <div className="menu-item-sub" onClick={enableMaterial?()=>{
                            this.setState({isMaterialMobileOpen:!this.state.isMaterialMobileOpen})
                            this.setState({isCategoryMobileOpen:false})
                            this.setState({isSecretMobileOpen:false})
                            }:()=>{}}>
                            {this.props.userChoice?this.props.userChoice.material==''?'MATERIAL':this.props.userChoice.material:'MATERIAL'}
                            <img src={dropdown_icon} className="dropdown-icon"/>    
                        </div>
                        {this.state.isMaterialMobileOpen&&(this.props.userChoice.material?remainingTypes(stringToType(this.props.userChoice.material)).map((c:any)=>{
                            return ( <div className="menu-item-sub menu-item-sub-black" onClick={()=>this.handleClickedItem(c,"material")}>{c.displayName}</div>)
                        }):remainingTypes(TYPE.GOLD).map((c:any)=>{
                            return ( <div className="menu-item-sub menu-item-sub-black" onClick={()=>this.handleClickedItem(c,"material")}>{c.displayName}</div>)
                        }))}
                </div>}
           </div>
        );
    }
}
