import React from 'react';
import './MorseTextInput.scss';
import { ALPHA_TO_MORSE, MORSE_TO_IMG, isMobile } from '../../../assets/Constants';
import circle from '../../../assets/oval.svg';
import dash from '../../../assets/rectangle.svg';
import red_circle from '../../../assets/red-oval.svg';
import red_dash from '../../../assets/red-rectangle.svg';
import { ChainDetails } from '../../../models/Interfaces';




const  MAX_LENGTH =  100;
export const DOT_PRICE = 560;
export const DASH_PRICE = 980;
interface Props {
    className:string;
    hidePlaceholder?:boolean;
    onChange:(chain:string,indexError:number,chainDetails:ChainDetails)=>void;
    inputRef:(inputRef:any)=>void;
}

interface State  {
    text:string;
    dashLength:number;
    dotLength:number;
    chainLength:number;
    indexError:number;
    focus:boolean;

}

export class MorseTextInput extends React.Component<Props,State>{


    
    constructor(props:Props){
        super(props);
        this.state={text:'',dashLength:0,dotLength:0,chainLength:0,indexError:-1,focus:true}
    }

    handleChange = async (event:any)=>{
                if(/^(([a-zA-Z]{1,})\s{0,1})*$/m.test(event.target.value.toString())){
                    const value:string = event.target.value;

                    ///////////////////////
                    await this.setState({text:value,dashLength:0,dotLength:0,chainLength:0,indexError:-1})
                    let morseSum = 0;
                    let dotSum = 0;
                    let dashSum = 0;
                    let charSum = 0;
                    value.split('').map(async (c:string,index)=>{
                        const morseChar = ALPHA_TO_MORSE[c.toUpperCase()].split('');
                        if(this.state.indexError==-1)
                            charSum++;
    
    
                        if(this.state.indexError==-1)
                        morseChar.forEach((char)=>{
                            if(this.state.indexError==-1) { 
                             
                            if(char == '.'){
                                this.setState({dotLength:this.state.dotLength+3,chainLength:this.state.chainLength+3,})
    
                                morseSum += 3;
                                dotSum+=1;
    
                            }else if(char =='-'){
                                this.setState({dashLength:this.state.dashLength+7,chainLength:this.state.chainLength+7})
                                morseSum += 7;
                                dashSum+=1;
                            }
                            else if(char ==' '){
                                this.setState({chainLength:this.state.chainLength+2.5})
                                morseSum += 2.5;
                            }
                            if(morseSum>MAX_LENGTH&&this.state.indexError==-1){
                                this.props.onChange(value.slice(0,charSum),index,{length:morseSum,dotSum:dotSum,dashSum:dashSum,totalPrice:(dotSum*DOT_PRICE+dashSum*DASH_PRICE)});
                                 this.setState({indexError:index});
                            }
                           
                            this.props.onChange(value.slice(0,charSum),this.state.indexError,{length:morseSum,dotSum:dotSum,dashSum:dashSum,totalPrice:(dotSum*DOT_PRICE+dashSum*DASH_PRICE)});
                        }
                      
                      })
                        return morseChar.join('')
                    }).join('');
    
                    if(value.length==0){
                        this.props.onChange(value,-1,{length:0,dotSum:0,dashSum:0,totalPrice:0});
                    }
                 }
    }

    render(){
        return (<div className={"input-wrapper-all "+this.props.className}>
            <div className={"morse-text-input-div"} style={{border:5,borderColor:"black"}}>
            <input autoFocus={this.state.focus}
            ref={(input) => {  this.props.inputRef(input)}} 
             style={{textIndent:this.state.text.length<1?'-35vw':'0'}}
              className={"morse-text-input"} value={this.state.indexError===-1?this.state.text:this.state.text.slice(0,this.state.indexError)} onChange={this.handleChange}/>
            {this.state.text.length === 0 && !this.props.hidePlaceholder && <div className="placeholder">TYPE</div>}
            <Morse text={this.state.text} indexError={this.state.indexError}/>
            </div>

            <div className={"error-position"+(this.state.indexError==-1?" invalid-validations":'')}>
                <div className="error-message">
                {`The morse Secret is too long!`.toLocaleUpperCase()}
                </div>
                {!isMobile && <div className="suggestions">
                &nbsp;{` Here are some other suggestions:`.toLocaleUpperCase()}<br/>
                {`be mine Love Marry me Candy`.toLocaleUpperCase()}
                </div>}
            </div>

        </div>

        );
    }
}

export const Morse = React.memo((props: {text: string, indexError: number }) => {
    return (
        <div className="text-span-input  "
             dangerouslySetInnerHTML={{__html:`${
                     props.text.slice(0,props.indexError!==-1?(props.indexError+1):props.text.length).split('').map((c:string,index:number)=>{
                         return `<span class="span-char-input ${props.text.length<8?'':props.text.length<14?'long-text-width':'too-long-text-width'}
                     ${index===props.indexError&&props.indexError!=-1?"red":''}">${c}`+`<span class="char" >${ALPHA_TO_MORSE[c.toUpperCase()].split('').map((cc:string)=>{

                             if(cc=='.'){
                                 if(index===props.indexError&&props.indexError!=-1)
                                     return `<img src=${red_circle} class="icon dot-width-input"  />`
                                 return `<img src=${circle} class="icon dot-width-input"  />`
                             }
                             else if(cc =='-'){
                                 if(index===props.indexError&&props.indexError!=-1)
                                     return `<img src=${red_dash} class="icon dash-width-input" />`
                                 return `<img src=${dash} class="icon dash-width-input"  />`
                             }else if( cc==' '){
                                 return ``;
                             }
                             return false;
                         }).join('')}</span></span>`
                     }).join('')}`}}>
        </div>
    );
})
